@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");

:root {
  --font-main: "Mont", Arial, Helvetica, sans-serif;
  --font-second: "Roboto", Arial, Helvetica, sans-serif;
  --font-size: 16px;
  --font-size-var1: clamp(18px, 1.85vw, 24px);
  --font-size-var2: clamp(12px, 1.23vw, 16px);
  --color-bg: #ffffff;
  --color-white: #ffffff;
  --color-dark: #484848;
  --color-green: #12b992;
  --color-orange: #f8c227;
  --color-blue: #225980;
  --color-gray: #413e5e;
}
a {
  text-decoration: none !important;
  color: inherit;
}

html {
  background-color: var(--color-bg);
  margin-right: 0 !important;
}
body {
  font-family: var(--font-main);
  font-style: normal;
  font-weight: normal;
  font-size: var(--font-size-var2);
  color: var(--color-dark);
  
}
.wrapper {
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  min-height: 100%;
}
img {
  max-width: 100%;
}

.section {
  max-width: 100%;
  width: 100%;
  position: relative;
  &__content {
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 20px;
  }
}
._1 {
  .section__content {
    padding: 20px 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .container {
    width: 100%;
    display: flex;
    padding: 0 20px;
  }
  .row {
    display: flex;
    width: 600px;
    justify-content: space-between;
    margin-bottom: 1rem;
  }

  .col-header {
    width: 150px;
    box-shadow: 4px 4px 9px rgba(198, 198, 198, 0.36);
    border-radius: 20px;
    padding: 0.8rem 1rem;
    color: #fff;
  }

  .item {
    width: 150px;
    height: 66px;
    border: 1px solid #eee;
    box-shadow: 4px 4px 9px rgba(198, 198, 198, 0.36);
    border-radius: 20px;
    background: #f7f6f7;
    padding: 0.8rem 1rem;
    color: #828282;
    text-align: center;
    cursor: grab;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .item:active {
    cursor: grabbing;
    border: 1px solid #eee;
    box-shadow: 4px 4px 9px rgba(198, 198, 198, 0.36);
    border-radius: 20px;
  }

  .placeholder {
    width: 184px;
    border-radius: 20px;
    height: 94px;
  }

  .start {
    background: linear-gradient(90deg, #ff85e4 0%, #229efd 179.25%);
  }

  .progress {
    background: linear-gradient(90deg, #209cff 0%, #68e0cf 100%);
  }

  .done {
    background: linear-gradient(90deg, #84fab0 0%, #8fd3f4 100%);
  }
  .hold {
    background: #68e0cf;
    color: #ffffff;
  }
  .hovered {
    border: 1.5px dashed #68e0cf;
  }
  .hide {
    display: none;
  }
}
